<template>
    <div class="cartcontrol">
        <transition name="move">
            <div class="cart-decrease" 
                 v-show="food.count>0" @click.stop.prevent="decreaseCart">   
                <span class="inner icon-remove_circle_outline"></span>
            </div>
        </transition>
        <div class="cart-count" 
             v-show="food.count>0">
            {{ food.count }}
        </div>
        <div class="cart-add icon-add_circle"
             @click.stop.prevent="addCart">
        </div>
    </div>
</template>

<script>
import Vue from 'vue'
export default {
    props: {
        food: {
            type: Object
        }
    },
    created() {
        // console.log(this.food);
    },
    methods : {
        addCart (event) {
            // 判断pc端两次点击
            if (!event._constructed) {
                return;
            }
            if(!this.food.count) {
                /* this.food.count = 1;
                此方式有问题，在vue内用以下方法 */
                    Vue.set(this.food,'count',1)
            } else {
                    this.food.count++;
            }
            // 点击添加按钮生成一个事件 add事件
            this.$emit('cart-add',event.target);
        },
        decreaseCart (event) {
            if(!this.food.count) {
                /* this.food.count = 1;
                此方式有问题，在vue内用以下方法 */
                Vue.set(this.food,'count',1)
            } else {
                this.food.count--;
            }
        },
    }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
    .cartcontrol
        font-size 0
        .cart-decrease
            display inline-block
            padding 6px
            // font-size 24px
            // line-height 24px
            // color rgb(0,160,220)
            // text-align center
            transition all 0.4s linear
            transform translate3d(0,0,0)
            opacity:1
            .inner
                display inline-block
                font-size 24px
                line-height 24px
                color rgb(0,160,220)
                text-align center
                transition all 0.4s linear
                transform rotate(0)
            &.move-enter, &.move-leave-active{
                transition all 0.4s linear
                opacity 0
                transform translate3d(24px, 0, 0)
                .inner{
                    transform: rotate(180deg)
                }
            }
            
        .cart-count
            display inline-block
            vertical-align top
            text-align center 
            font-size 10px
            line-height 24px
            padding-top: 6px
        .cart-add
            display inline-block
            padding 6px
            font-size 24px
            line-height 24px
            color rgb(0,160,220)
            text-align center

 </style>



